import { AnchorHTMLAttributes } from 'react';
import { InjectedCompType, useBook } from '@topwrite/common';
import path from 'path';
import useArticle from '@/lib/use-article';
import Link from '@/components/link';

const Anchor: InjectedCompType<AnchorHTMLAttributes<HTMLAnchorElement>> = ({ props: { href, ...props } }) => {

    const { summary } = useBook();
    const article = useArticle();

    if (href) {
        if (path.extname(href) === '.md') {
            let linkRef;
            if (article) {
                linkRef = decodeURIComponent(path.join(path.dirname(article.ref), href));
            } else {
                linkRef = href;
            }

            const linkArticle = summary.getArticle(article => article.ref === linkRef);
            if (linkArticle) {
                return <Link to={linkArticle} {...props} />;
            }
        }

        if (href.startsWith('http')) {
            props.target = '_blank';
        }
    }
    return <a href={href} {...props} />;
};

export default Anchor;
