import useTitle from '@/lib/use-title';
import { LetterAvatar, useSelector } from '@topwrite/common';
import { useState } from 'react';

interface LogoProps {
    className?: string
    size?: number
}

export default function Logo({ size = 30, className }: LogoProps) {

    const title = useTitle();
    const file = useSelector('file');
    const logo = file.relative('logo.png');

    const [error, setError] = useState<boolean | string>(false);

    const handleError = () => {
        try {
            const dataUrl = (new LetterAvatar(title, size)).toDataUrl();
            setError(dataUrl);
        } catch {
            setError(true);
        }
    };

    if (error) {
        if (typeof error === 'string') {
            return <img className={className} src={error} width={size} height={size} />;
        }
        return null;
    }

    return <img className={className} src={logo} width={size} height={size} onError={handleError} />;
}
